.page-contents-listing-menu-item {
	min-width: 30%;
	height: 240px;
	flex: 1 1 auto;
	display: flex;
	justify-content: center;
	align-items: center;
	border: 1px solid black;
	margin: 0 7.5px 15px;
	background-position: center;
	background-size: cover;

	&:first-child {
		margin-right: 7.5px;
	}

	&.last-child {
		margin-left: 7.5px;
	}
}

.content {
	height: 90px;
	padding: 0 25px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	border: 1px solid black;
	background-color: whitesmoke;
	color: black;
	position: absolute;

	&:hover {
		cursor: pointer;
	}

	.page-contents-listing-items-title {
		font-weight: bold;
		margin-bottom: 6px;
		font-size: 22px;
		color: #4a4a4a;
	}

	.page-contents-listing-items-subtitle {
		font-weight: lighter;
		font-size: 16px;
	}
}